body {
  margin: 0;
  box-sizing: border-box;
  background-color: black;
}

.Header{
  height: 300px;
  width: 100%;
font-size: 35px;
 color: violet;
 background-color: black;
 display: flex;
 justify-content: center;
 align-items: center;
 flex-direction: row;
}
.Header > img{
  width: 200px;
  margin: 10px 60px;
}
.meme-form{
  color: aqua;
  background-color: black;
  width: 100%;
  height:200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.meme-form > input{
  padding: 10px 30px;
  color: violet;
}
.meme{
 width: 80%;
 height: 500px;
 position: relative;
 background-color: black;
 margin: auto;
}
.meme > img{
  width: 100%;
  max-height: 100%;

}
.meme >h2{
  position: absolute;
  width: 80%;
  color: rgb(248, 245, 245);
  /* text-align  left transform for centering the text even what ever you are typing*/
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  font-family: 'Fondamento', cursive;
  padding: 0 5px;
  text-transform: uppercase;
  letter-spacing: 1px;

}
 .top{
  top: 0;
}
.bottom{
  bottom: 0;
}
.meme-form > button{
  padding: 10px;
  color: violet;
  font-size: 20px;
  margin-bottom: 5px;
}